.Sudoku {
    width: 100%;
    height: 100%;
}

#grid {
    width: 100%;
    height: 100%;

    display: grid;

    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

.BigCell {
    justify-self: center;
    align-self: center;

    min-width: 10px;
    min-height: 10px;

    height: 100%;
    width: 100%;

    display: grid;

    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

.Cell {
    box-sizing: border-box;

    height: 100%;
    width: 100%;

    background-color: indianred;
    border: 1px solid black;
}
